body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  font-family: 'Raleway', sans-serif;
  color: #1C3E6E;
}

a {
  text-decoration: none;
}

.link {
  text-decoration: none;
}

p {
  color: #8198B8;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.footer img {
  /* make the footer appear in the centre of the page
  */
  background-color: white;
  color: white;
  text-align: center;
  width: 50%;
  height: 30%;
  margin-top: 150px;
  /* make the footer appear in the centre of the page
  */
}

.page {
  position: absolute;
  left: 15px;
  right: 15px;
}

.page-enter {
  opacity: 0;
  transform: scale(1.08);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}

.page-exit {
  opacity: 1;
  transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  transform: scale(0.92);
  transition: opacity 300ms, transform 300ms;
}

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  border-radius: 16px;
}
 
::-webkit-scrollbar-thumb {
  background: #1C3E6E; 
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #2a63b3; 
}
.navbar {
  width: 100vw;
  background: #1c3e6f;
  height: 80px;
  position: fixed;
  z-index: 100;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  font-weight: 700;
}

.navbar-left-container {
  color: white;
  font-size: 34px;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 30px;
}

.navbar-right-container {
  margin-right: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.navbar-img {
  height: 60px;
  width: 60px;
  margin-right: 10px;
}

.about-btn-outer {
  background-color: white;
  height: 55px;
  width: 55px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
}
.about-btn-outer:hover {
  transform: scale(1.03);
  transition: transform 200ms;
}
.about-btn-outer:active {
  transform: scale(0.97);
  transition: transform 100ms;
}

.about-btn-inner {
  background-color: #1c3e6f;
  color: white;
  height: 30px;
  width: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}/*# sourceMappingURL=Navbar.css.map */
.button, .secondary-button, .primary-button {
  display: inline-block;
  border-radius: 14px;
  padding: 14px 30px;
  text-align: center;
  font-size: 17px;
  font-weight: 700;
  cursor: pointer;
  transition: transform 200ms, background-color 200ms;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.button:hover, .secondary-button:hover, .primary-button:hover {
  transform: scale(1.03);
  transition: transform 200ms;
}
.button:active, .secondary-button:active, .primary-button:active {
  transform: scale(0.97);
  transition: transform 100ms;
}

.primary-button {
  background-color: white;
  color: #1c3e6f;
}

.secondary-button {
  color: white;
  background-color: #1c3e6f;
}

.icon-button {
  width: 57px;
  height: 57px;
  margin-left: 5px;
}

.button-icon {
  width: 28px;
  height: 28px;
  margin: 0 -16px;
  padding: 0;
}/*# sourceMappingURL=Button.css.map */
.text-input-search {
  font-size: 17px;
  background-color: white;
  border-radius: 30px;
  margin: 4px;
  padding: 12px 40px;
  width: 100%;
  box-shadow: 0px 2px 6px 3px rgba(46, 46, 46, 0.4156862745);
  outline: none;
  border: none;
  transition: box-shadow 300ms, padding 300ms;
}
.text-input-search:-ms-input-placeholder {
  -ms-transition: opacity 300ms, transform 700ms;
  transition: opacity 300ms, transform 700ms;
}
.text-input-search::placeholder {
  transition: opacity 300ms, transform 700ms;
}
.text-input-search:focus:-ms-input-placeholder {
  opacity: 0;
}
.text-input-search:focus::placeholder {
  opacity: 0;
}

.text-input-search-placeholder-transition:-ms-input-placeholder {
  transform: translateX(10px);
  opacity: 0;
}

.text-input-search-placeholder-transition::placeholder {
  transform: translateX(10px);
  opacity: 0;
}

.text-input-search:hover {
  box-shadow: 0px 1px 4px 2px rgba(46, 46, 46, 0.4156862745);
  padding: 12px 40px;
  transition: box-shadow 500ms, padding 500ms;
}
.text-input-search:focus {
  box-shadow: 0px 0px 6px 4px rgba(101, 101, 101, 0.2784313725);
  padding: 12px 30px;
  transition: box-shadow 500ms, padding 500ms;
}/*# sourceMappingURL=Searchbar.css.map */
.home-page-bg {
  background-image: url(/static/media/ConHall.35de1f13.png);
  height: 100vh;
  background-position: center;
