.courseInfo-tab, .secondary-tab, .primary-tab {
  display: flex;
  align-items: center;
  border-radius: 10px;
  height: 50px;
  padding-left: 13px;
  margin-top: 10px;
}
.courseInfo-tab:hover, .secondary-tab:hover, .primary-tab:hover {
  transform: scale(1.03);
  transition: transform 200ms;
  cursor: pointer;
}
.courseInfo-tab:active, .secondary-tab:active, .primary-tab:active {
  transform: scale(0.97);
  transition: transform 100ms;
}

.primary-tab {
  width: 270px;
}

.tab-collapse {
  width: 58px;
}
.tab-collapse .tab-text {
  display: none;
}

.secondary-tab {
  width: 58px;
  padding-left: 22px;
}

.icon-container {
  width: 55px;
}

.active-text {
  color: white;
}

.inactive-text {
  color: #243e6b;
}/*# sourceMappingURL=CourseInfoTab.css.map */
